import React from "react";
import { useState } from "react";
import Booking from "../../pages/Booking"

export default function Calendar() {
    const [ openState, setOpenState ] = useState({
        isOpen: false
    });
    function showBooking() {
        setOpenState( state => ({
            isOpen: !state.isOpen
        }))
    };

    return (
        <section id="calendar">
            <div class="text-block">
                <h2>Book a Session</h2>
                <p>Click below to view availability and pricing. Please note, in studio appointments are only available on days listed in the appointment type.</p>
                <p>If you are interested in booking an in studio session with Brittney, please email <a href="mailto:brittney@eskewconsulting.com">brittney@eskewconsulting.com</a>. Only zoom appointments can be booked online.</p>
                <button onClick={()=> showBooking()} className="cta transition">Book a session</button>
            </div>
            <Booking className={openState.isOpen ? "book-now" : ""}/>
            
        </section>
    )
}